import React, {useState} from 'react'
import s from './EditData.module.scss'
import { Field, reduxForm} from "redux-form";
import { requiredField, minLength3, maxLength50 } from '../../../../utils/validators/validators';
import {Checkbox, Input, Textarea} from "../../../common/Forms/Controls";
import Preloader from '../../../common/Preloader';
import Icon from "../../../common/Icon";

export default props => {
    const {myId, profile, profile: {userId}, updateProfileData} = props;

    if(Number(myId) !== Number(userId)) return null;

    const [edit, setShowEdit] = useState(false);

    const showEdit = () => {
        setShowEdit(!edit)
    };

    const submitProfileData = data => {
        updateProfileData(data);
    };

    if(!edit) return <span onClick={showEdit}><Icon icon={'pencil'} className={s.ShowIcon}/></span>;


    return (
        <div className={s.EditData}>
            <span onClick={showEdit}><Icon icon={'close'} className={s.CloseIcon}/></span>
            <UpdateProfileFormRedux initialValues={profile} profile={profile}  onSubmit={ data => submitProfileData(data)} onClose={showEdit}/>
        </div>
    );
};

const UpdateProfileForm = props => {
    const {handleSubmit, invalid, profile, onClose} = props;

    if(!profile) return Preloader({loading: true});

    const {contacts} = profile;

    return (
        <form onSubmit={handleSubmit} className={'Form'}>
            <h3>Обо мне</h3>

            <Field
                label={'Имя'}
                component={Input}
                name={'fullName'}
                validate={[requiredField, minLength3, maxLength50]}
            />

            <Field
                component={Textarea}
                name={'aboutMe'}
                validate={[requiredField, minLength3]}
                label={'Текст обо мне'}
            />

            <div className={s.Collapse + ' ' + s.Contacts}>
                <h3 className={s.FormTitle}
                    onClick={e => e.currentTarget.classList.toggle(s.open)}
                >
                    Контакты
                    <Icon className={s.CollapseIcon} icon={'arrow-right'}/>
                </h3>
                <div className={s.Collapsable}>
                    {
                        Object.keys(contacts).map( key =>
                            <Field
                                key={key}
                                component={Input}
                                name={'contacts.'+key}
                                validate={[minLength3]}
                                label={key}
                            />
                        )
                    }
                </div>
            </div>

            <div className={s.Collapse}>
                <h3 className={s.FormTitle}
                    onClick={e => e.currentTarget.classList.toggle(s.open)}
                >
                    Работа
                    <Icon className={s.CollapseIcon} icon={'arrow-right'}/>
                </h3>
                <div className={s.Collapsable}>
                    <Field
                        type={'checkbox'}
                        component={Checkbox}
                        name={'lookingForAJob'}
                        label={'Ищу работу'}
                    />

                    <Field
                        component={Textarea}
                        name={'lookingForAJobDescription'}
                        validate={[requiredField, minLength3]}
                        label={'Текст о работе'}
                    />
                </div>
            </div>

            {props.error && props.error.message &&
                <div className={'Form_Row'}>
                    <p className={'Form_Error'}>
                        {props.error.message}
                        <Icon icon={'alert'} className={'Form_ErrorIcon'}/>
                    </p>
                </div>
            }

            <div className={'Form_Row Form_Row__TextRight Form_Footer'}>
                <button type={'button'} className={'Form_Button Form_Button__warning'} onClick={onClose}>Отменить</button>
                <button type={'submit'} className={'Form_Button'} disabled={invalid}>Сохранить</button>
            </div>
        </form>
    )
};

let UpdateProfileFormRedux = reduxForm({
    form: 'update-profile-form'
})(UpdateProfileForm);
