import React from 'react';
import s from '../ProfileInfo.module.scss';

export default ({contacts}) => {
    const arr = [];

    const parseUrl = url => {
        const match = url.match(/(^https?)?(:?\/\/)?((?:[\w-]+)(?:\.[\w-]+)+)(?:[\w.,@?^=%&amp;:/~+#-]*[\w@?^=%&amp;/~+#-])?/);
        return {
            href: (match[1] !== undefined ? match[1]: 'https') + (match[1] !== undefined ? match[2]: '://') + match[3],
            value: match[3]
        };
    };

    Object.keys(contacts).forEach( key => {
        if(contacts[key]) {
            const {href, value} = parseUrl(contacts[key]);
            arr.push(
                <div key={key}>
                    <b>{key}: </b><a href={href} target={'_blank'} rel="noopener noreferrer">{value}</a>
                </div>
            )
        }
    });
    if(!arr.length) return null;
    return (
        <div className={s.contacts}>
            <div className={s.contactsTitle}>Contacts</div>
            {arr}
        </div>
    )
};
